'use strict';

angular.module('queryBuilder.directives')
.directive('ruleSize', function () { 
    return {
      require: 'ngModel',
      link: function(scope, elem, attr, ngModel) {

        
        function validate() {
          let rules = ngModel.$modelValue.rules;
          if (rules.length > 1) {
            ngModel.$setValidity('invalidRuleSize', true);
          } else {
            ngModel.$setValidity('invalidRuleSize', false);
          }
        }
        
        let ruleSizePresent = attr.ruleSize;
        if (ruleSizePresent) {
          scope.$watch(function(newValue, oldValue) {
            validate();
          });
        }
      }
    };
  });