'use strict';

angular.module('queryBuilder.services')
.factory('fieldDataService', function() {

    return {
        getFields: getFields
    };

    function getFields() {
        let filters = [
            {
                id: 1,
                name: 'Attribution',
                values: [
                    'COMMON',
                    'CAT',
                    'DOG'
                ],
                options: {
                    allowCaseSensitive: false,
                    multipleValues: true,
                    validOperators: ['exact-match']
                }
            },
            {
                id: 2,
                name: 'Class',
                values: [],
                options: {
                    allowCaseSensitive: true
                }
            },
            {
                id: 3,
                name: 'Type',
                values: [],
                options: {
                    allowCaseSensitive: true
                }
            }
        ];

        // Populate class and type with some data
        for (let i = 1; i <= 100; i++) {
            filters[1].values.push('Class' + i);
            filters[2].values.push('Type' + i);
        }

        return filters;
    }

});