'use strict';

/**
 * A component to allow the construct of both simple and complex filters.
 * 
 * The bindings are:
 * @param {boolean} allowEmptyRules Indicates if empty rules are allowed. Defaults to false.
 */
angular.module('queryBuilder.components')
.component('queryBuilder', {
    template: require('./query-builder.component.html'),
    bindings: {
        allowEmptyRules: '<?',
        childRule: '<?',
        childRuleSet: '<?',
        conditionOptions: '<?',
        data: '=?',
        displayOptions: '<?',
        fields: '<?',
        initialCondition: '@?',
        mixRules: '<?',
        mode: '@?',
        operators: '<?',
        preDefinedRules: '<?',
        singleRuleSet: '<?'
    },
    controller: [queryBuilderController]
});

function queryBuilderController() {
    let vm = this;
    vm.$onInit = onInit;
    
    /**
     * Initialisation of this component.
     */
    function onInit() {
        if (vm.allowEmptyRules === undefined) {
            vm.allowEmptyRules = true;
        }
        vm.data = vm.data || {
            condition: 'and',
            rules: []
        };
        vm.conditionOptions = vm.conditionOptions || {
            'and': {
                rule: true,
                ruleSet: true
            },
            'or': {
                rule: true,
                ruleSet: true
            },
            'criteria': {
                rule: false,
                ruleSet: true
            }
        };
        vm.displayOptions = vm.displayOptions || {
            entityTerms: {
                rule: 'Rule',
                ruleSet: 'Rule Set'
            },
            includeModel: false,
            logicalTerms: {
                and: 'AND',
                or: 'OR'
            },
            useTextButtons: false
        };
        vm.mode = vm.mode || 'build';
        vm.showModel = false;
        vm.userInputNames = [];
    }

}